  const reducer = (state = initialState, action) => {
    switch (action.type) {
      case SLICE_BOOKS:
        return {
          ...state,
          sliceBooks: state.books.splice(0, 10),
        };
      default:
        return { ...state };
    }
  };
  
  export default reducer;